import React, { Component } from 'react';
import classes from './Roadmap.css';

class RoadmapDotConnector extends Component {
  render() {
    return (
      <div className={`${classes['roadmap-dot-connector']}`}></div>
    );
  }
}

export default RoadmapDotConnector;


